import { useState, useEffect } from 'react';

interface MetaStats {
    balance: number;
    equity: number;
    margin: number;
    freeMargin: number;
    isConnected: boolean;
}

export const useMetaStats = (accountId: string | null) => {
    const [stats, setStats] = useState<MetaStats>({
        balance: 0,
        equity: 0,
        margin: 0,
        freeMargin: 0,
        isConnected: false,
    });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        if (!accountId) return;

        // Mocking the connection for now as we don't have a live WS endpoint on the backend yet
        // In a real implementation this would connect to the backend WS proxy or MetaAPI directly
        const mockConnect = () => {
            setLoading(true);
            setTimeout(() => {
                setStats({
                    balance: 10450.00,
                    equity: 10620.50,
                    margin: 250.00,
                    freeMargin: 10370.50,
                    isConnected: true
                });
                setLoading(false);
            }, 1000);
        };

        mockConnect();

        // Polling interval (simulating live updates)
        const interval = setInterval(() => {
            setStats(prev => ({
                ...prev,
                equity: prev.equity + (Math.random() - 0.4) * 10, // Simulate fluctuation
                isConnected: true
            }));
        }, 1000);

        return () => clearInterval(interval);
    }, [accountId]);

    return { stats, loading, error };
};
